import React from 'react';
import styled from 'styled-components';
import logo from './assets/logo-footer.svg'
import signature from './assets/signature.svg'

import {breakpoint, BreakPoint, Button} from '@aragon/ui';
const medium = css => breakpoint('medium', css);

const Footer = () => (
  <FooterSection>
    <Container>
      <div>
        <A1Logo src={logo}/>
        <a href="https://blog.aragon.one" target="_blank">Blog</a>
        <a href="https://twitter.com/AragonOneTeam" target="_blank">Twitter</a>
        <a href="https://aragon.org" target="_blank">Aragon Project</a>
      </div>
      <div>
        <img src={signature}/>
      </div>
    </Container>
  </FooterSection>
);

const FooterSection = styled.section`
  background-color: black;
  height: auto;
  margin: 30px 0 30px 0;
  text-align: center;
  overflow: hidden;
`;
const A1Logo = styled.img`
  ${medium('padding-right: 15px;')};
`
const Container = styled.div`
  width: 80%;
  margin: auto;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-top: 30px;
  flex-direction: column;
  ${medium('flex-direction: row;')};
  border-top: solid 1px #3A3A3A;
  a {
    font-family: 'FontRegular';
    line-height: 2;
    font-size: 1rem;
    letter-spacing: 1.6px;
    text-transform: uppercase;
    padding: 0px 15px;
    cursor: pointer;
    display: block;
    margin: 10px 0;
    color: white;
    text-decoration: none;
    ${medium('display: inline; margin: 0;')};

  }
  a:hover {
    opacity: 0.8;
  }
`;

export default Footer;



